<template>
  <el-card shadow="hover">
    <img :src="token.logo" class="logo" alt="">
    <div class="content">
      <span class="title">{{token.name}}</span>
      <p class="summary">{{token.desc}}</p>
      <p class="time">{{token.openingTime}} ~ {{token.endingTime}}</p>
      <div class="info">
        <p>: {{token.totalSupply}} {{token.symbol}}</p>
        <p>: {{token.cap}} {{token.symbol}}</p>
        <p>: {{token.rate}}NAS/{{token.symbol}}</p>
      </div>
    </div>
    <div style="text-align:right">
      <el-button size="mini" type="danger" @click="emitBuy" :disabled="checkInvaild">{{checkInvaild ? "" : ""}}</el-button>
    </div>
  </el-card>
</template>


<script>
import moment from "moment";
export default {
  data() {
    return {};
  },
  props: ["token"],
  methods: {
    emitBuy() {
      this.$emit("buy", this.token);
    }
  },
  computed: {
    checkInvaild() {
      const openingTime = moment(this.token.openingTime, "YYYYMD");
      const endingTime = moment(this.token.endingTime, "YYYYMD");
      const now = moment();
      if (openingTime.isAfter(now) || endingTime.isBefore(now)) return true;
      else return false;
    }
  }
};
</script>


